import { FC, useState } from "react";
import { MeetingsProps } from "./types-meetings";
import {
  Button,
  ButtonCard,
  PlanCard,
  Title,
  Wrapper,
} from "./styles-meetings";
import Assembly from "./assembly/assembly";
import FilterButton from "./filter-button/filter-button";
import OpenAssembly from "../../common/open-assembly/open-assembly";
import CreateMeeting from "../../common/create-meeting/create-meeting";
import { GeneralContainer, UniversalModal } from "@components/common";

const Meetings: FC<MeetingsProps> = () => {
  const [forAssembly, setForAssembly] = useState<boolean>(true);
  const [openModalAssembly, setOpenModalAssembly] = useState<boolean>(false);
  const [openCreateMeeting, setOpenCreateMeeting] = useState<boolean>(false);
  return (
    <GeneralContainer>
      <Wrapper>
        <Title>{forAssembly ? "Majlis" : "Uchrashuv"}</Title>
        <OpenAssembly
          open={openModalAssembly}
          handleClose={() => setOpenModalAssembly(false)}
        />

        <CreateMeeting
          open={openCreateMeeting}
          handleClose={() => setOpenCreateMeeting(false)}
        />
        <ButtonCard optional>
          <ButtonCard optional={false}>
            <Button active={forAssembly} onClick={() => setForAssembly(true)}>
              Majlislar
            </Button>
            <Button
              active={!forAssembly}
              style={{ marginLeft: "23px" }}
              onClick={() => setForAssembly(false)}
            >
              Uchrashuvlar
            </Button>
            <FilterButton />
          </ButtonCard>
          <div>
            <Button
              active
              onClick={() => {
                forAssembly
                  ? setOpenModalAssembly(true)
                  : setOpenCreateMeeting(true);
              }}
            >
              + {forAssembly ? "Majlis Yaratish" : "Uchrashuv Yaratish"}
            </Button>
          </div>
        </ButtonCard>
        <PlanCard>
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
          <Assembly forAssembly={forAssembly} />
        </PlanCard>
      </Wrapper>
    </GeneralContainer>
  );
};

export default Meetings;
