import { FC } from 'react';
import { Wrapper, Card, Icon, Label } from './styles-assembly';
import { AssemblyProps } from './types-assembly';
import red_calendar from '@assets/images/meetings/calendar-red.svg';
import red_group from '@assets/images/meetings/group-red.svg';
import red_supperGroup from '@assets/images/meetings/supperGroup-red.svg';
import red_edit from '@assets/images/meetings/edit-red.svg';
import profile from '@assets/images/meetings/Profile.svg';
import watch from '@assets/images/meetings/Watch.svg';
import location from '@assets/images/meetings/Location.svg';
import blue_profile from '@assets/images/meetings/blue-Profile.svg';
import blue_watch from '@assets/images/meetings/blue-Watch.svg';
import blue_location from '@assets/images/meetings/blue-Location.svg';
import blue_supperGroup from '@assets/images/meetings/blue-supperGroup.svg';
import blue_calendar from '@assets/images/meetings/blue-calendar.svg';

const mock = [
  {
    red_icon: red_supperGroup,
    blue_icon: blue_supperGroup,
    label: '2131887182 #39',
  },
  {
    red_icon: red_edit,
    label: 'Majlis nomi',
  },
  {
    red_icon: red_calendar,
    blue_icon: blue_calendar,
    label: '30.04.2022',
  },
  {
    red_icon: watch,
    blue_icon: blue_watch,
    label: '17:00',
  },
  {
    red_icon: red_group,
    label: '15 ta ishtirokchi',
  },
  {
    red_icon: profile,
    blue_icon: blue_profile,
    label: 'Ibrohim Gulyamov',
  },
  {
    red_icon: location,
    blue_icon: blue_location,
    label: 'Chilonzor, 9KV 11a uy',
  },
];

const Assembly: FC<AssemblyProps> = ({ forAssembly }) => {
  return (
    <Wrapper>
      {mock.map(({ red_icon, label, blue_icon }, index) => {
        if (forAssembly) {
          return (
            <Card key={index}>
              <Icon src={red_icon} />
              <Label isTitle={index === 0}>{label}</Label>
            </Card>
          );
        } else {
          if (!!blue_icon) {
            return (
              <Card key={index}>
                <Icon src={blue_icon} />
                <Label isTitle={index === 0}>{label}</Label>
              </Card>
            );
          }
        }
      })}
    </Wrapper>
  );
};

export default Assembly;
