import { FC, useState } from "react";
import { useForm } from "react-hook-form";
import {
  AuthorBtn,
  DateInput,
  Input,
  LightSelect,
  MuiSlider,
  Selector,
  StartToFinish,
  UniversalModal,
  UploadFile,
  ModalTopButton,
} from "@components/common";
import {
  AvatarImg,
  AvatarImgNone,
  ButtonCard,
  Card,
  FlexBox,
  Form,
  GroupCard,
  IconButton,
  Img,
  InputCard,
  RepeatSelectBox,
  SaveButton,
  SelectedStaffs,
  Title,
  TitleRepeat,
  WithWho,
  WithWhoWrap,
  Wrapper,
  WrapRepeatContent,
} from "./styles-create-meeting";
import { CreateMeetingForm, CreateMeetingProps } from "./types-create-meeting";
import profileAvatarIcon from "@assets/images/meetings/profile-avatar.svg";
import people_group from "@assets/images/meetings/people-group.svg";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
import plusIcon from "@assets/images/meetings/plus-icon-blue.svg";
import statusNew from "@assets/images/meetings/status-new.svg";
import note from "@assets/images/dashboard/note.svg";
import person from "@assets/images/dashboard/person.svg";

import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  Radio,
  RadioGroup,
} from "@mui/material";
import StatusBtn from "@components/common/status-btn/status-btn";
import { CloseButton } from "../open-assembly/styles-open-assembly";

const CreateMeeting: FC<CreateMeetingProps> = ({ open, handleClose }) => {
  const [repeatValue, setRepeatValue] = useState<string>("Bir marta");
  const [notifications, setNotifications] = useState<any>([]);
  const { handleSubmit, control } = useForm<CreateMeetingForm>({
    defaultValues: {
      withWho: "",
      description: "",
      locationName: "",
    },
  });
  const onChange = (type: string, value: any) => {
    console.log("type: ", type);
  };
  const weekDays = [
    { day: "Dushanba", value: 0 },
    { day: "Seshanba", value: 1 },
    { day: "Chorshanba", value: 2 },
    { day: "Payshanba", value: 3 },
    { day: "Juma", value: 4 },
    { day: "Shanba", value: 5 },
    { day: "Yakshanba", value: 6 },
  ];
  const handleEdit = (e: number, time: number | number[]) => {
    let sliderNotifications = [...notifications];
    let sliderNotifications1 = { ...notifications[e] };
    sliderNotifications1.time = time;
    sliderNotifications[e] = sliderNotifications1;
    setNotifications(sliderNotifications);
  };
  const handleRemove = async (e: number) => {
    let notif = notifications;
    notif = notif.filter((item: any, idx: number) => idx != e);
    setNotifications(notif);
  };
  const staffs = [
    {
      avatar:
        "https://images.unsplash.com/photo-1628890920690-9e29d0019b9b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTR8fGF2YXRhcnxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=400&q=60",
      id: 0,
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1628890920690-9e29d0019b9b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTR8fGF2YXRhcnxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=400&q=60",
      id: 1,
    },
    {
      avatar:
        "https://images.unsplash.com/photo-1628890920690-9e29d0019b9b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTR8fGF2YXRhcnxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=400&q=60",
      id: 2,
    },
    { avatar: "", id: 3 },
    {
      avatar:
        "https://images.unsplash.com/photo-1628890920690-9e29d0019b9b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTR8fGF2YXRhcnxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=400&q=60",
      id: 4,
    },
  ];
  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        <Title>Uchrashuv yaratish</Title>
        <CloseButton onClick={handleClose}>
          <img src={closeIcon} alt="icon" />
        </CloseButton>
        <ButtonCard>
          <ModalTopButton
            title="Muallif"
            icon={person}
            isTooltip={{ name: "Jahongir Majidov" }}
            onClick={() => console.log("clicked author")}
          />
          <ModalTopButton
            title="Fayllar"
            icon={note}
            isFile={{ count: 5 }}
            onClick={() => console.log("clicked file")}
          />
          <LightSelect />
          <ModalTopButton
            title="Status"
            icon={statusNew}
            onClick={() => console.log("clicked status")}
          />
        </ButtonCard>
        <Form>
          <GroupCard>
            <InputCard>
              <WithWhoWrap>
                <WithWho>
                  <p>Kim bilan</p>
                  <img src={people_group} alt="icon" />
                </WithWho>
                <SelectedStaffs>
                  {staffs.map((staff, idx) => {
                    return staff.avatar ? (
                      <AvatarImg src={staff.avatar} alt="icon" key={idx} />
                    ) : (
                      <AvatarImgNone
                        src={profileAvatarIcon}
                        alt="icon"
                        key={idx}
                      />
                    );
                  })}
                </SelectedStaffs>
              </WithWhoWrap>
            </InputCard>
          </GroupCard>
          <GroupCard>
            <InputCard>
              <Input
                name="description"
                forComment
                control={control}
                title=""
                placeholder="Izoh kiriting"
                onChange={onChange}
              />
            </InputCard>
          </GroupCard>
          <StartToFinish />
          <GroupCard>
            <InputCard>
              <Input
                name="locationName"
                control={control}
                title=""
                placeholder="Joy nomi"
                onChange={onChange}
              />
            </InputCard>
          </GroupCard>
        </Form>
        <Card style={{ alignItems: "start" }} marginTop="20px">
          <WrapRepeatContent>
            <RepeatSelectBox>
              <TitleRepeat>Takroriyligi</TitleRepeat>
              <Selector
                options={[
                  "Bir marta",
                  "Har kuni",
                  "Har hafta",
                  "Har oy",
                  "Har yil",
                ]}
                placeholder=" "
                selectedValue={setRepeatValue}
                marginTop="10px"
                defaultValue="Bir marta"
                withClearIcon
                isAutoComplete={false}
              />
            </RepeatSelectBox>
            {repeatValue !== "Bir marta" && (
              <RepeatSelectBox>
                <TitleRepeat style={{ marginBottom: "10px" }}>
                  Oxirgi eslatma vaqti
                </TitleRepeat>
                <DateInput />
              </RepeatSelectBox>
            )}
          </WrapRepeatContent>
          {repeatValue === "Har hafta" ? (
            <FormGroup>
              {weekDays.map((day) => (
                <FormControlLabel
                  control={<Checkbox defaultChecked={day.value === 0} />}
                  label={day.day}
                />
              ))}
            </FormGroup>
          ) : repeatValue === "Har oy" || repeatValue === "Har yil" ? (
            <FormControl>
              <RadioGroup
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="0"
                name="radio-buttons-group"
              >
                <FormControlLabel
                  value="0"
                  control={<Radio />}
                  label="Har oyning 30-sanasida"
                />
                <FormControlLabel
                  value="3"
                  control={<Radio />}
                  label="Har oyning oxirgi sanasida"
                />
                <FormControlLabel
                  value="1"
                  control={<Radio />}
                  label="Har oyning 5-Dushanbasida"
                />
                <FormControlLabel
                  value="2"
                  control={<Radio />}
                  label="Har oyning oxirgi Dushanbasida"
                />
              </RadioGroup>
            </FormControl>
          ) : (
            ""
          )}
          <div style={{ width: "64%" }}>
            <FlexBox>
              <TitleRepeat>Oldindan eslatma</TitleRepeat>
              <img
                src={plusIcon}
                onClick={() =>
                  setNotifications([
                    ...notifications,
                    { id: notifications.length, time: 10 },
                  ])
                }
                alt="icon"
              />
            </FlexBox>
            {notifications.map((notification: any, idx: number) => (
              <MuiSlider
                key={idx}
                id={idx}
                value={notification.time}
                onEdit={handleEdit}
                onRemove={handleRemove}
              />
            ))}
          </div>
        </Card>
        <SaveButton forSave>Saqlash</SaveButton>
        {/* 
        <Separator width="30px" />
        <Separator width="30px" />
        <LightButton
          icon={<Icon src={statistics_icon} border_radius />}
          text="Status"
        />
        <Separator width="30px" />

      <UniversalModal
        open={open}
        handleClose={handleClose}
        children={<PartnerSelection />}
      /> */}
      </Wrapper>
    </UniversalModal>
  );
};

export default CreateMeeting;
