import { FC, useState } from 'react';
import Menu from '@mui/material/Menu';
import { FilterButtonProps } from './types-filter-button';
import { Calendar } from '@components/common';
import { Icon } from '../assembly/styles-assembly';
import {
  AsButton,
  SelectionCard,
  Wrapper,
  Option,
  Selection,
} from './styles-filter-button';
import filter from '@assets/images/meetings/filter.svg';

const FilterButton: FC<FilterButtonProps> = () => {
  const [bySelection, setBySelection] = useState<boolean>(true);
  const [active, setActive] = useState<number>(0);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Wrapper>
      <AsButton
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <Icon src={filter} /> Filter
      </AsButton>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {bySelection ? (
          <>
            <SelectionCard>
              <Option active={active === 0} onClick={() => setActive(0)}>
                Bugun
              </Option>
              <Option active={active === 1} onClick={() => setActive(1)}>
                Oxirgi 7kun
              </Option>
              <Option active={active === 2} onClick={() => setActive(2)}>
                Oxirgi 31kun
              </Option>
              <Option active={active === 3} onClick={() => setActive(3)}>
                Oxirgi 1yil
              </Option>
            </SelectionCard>
            <Selection onClick={() => setBySelection(false)}>
              Tanlash orqali {'>'}
            </Selection>
          </>
        ) : (
          <Calendar />
        )}
      </Menu>
    </Wrapper>
  );
};

export default FilterButton;
